html {
  background-color: #011e26;
  overflow-x: hidden;
}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.App-header {
  background-color: black;
  position: relative;
  height: 35px;
  padding: 20px;
  border-bottom: 1px solid #002933;
  -webkit-box-shadow: black 0px 2px 17px 0px;
  box-shadow: black 0px 2px 17px 0px;
}

.logo-holder,
.side-right {
  position: absolute;
}

.logo-holder {
  left: 25px;
}

.side-right {
  right: 60px;
  width: 150px;
  text-align: right;
}

.brand {
  font-size: 30px;
  margin: 0px;
  padding: 0px;
  display: inline-block;
  vertical-align: top;
  color: #bbc9cc;
  font-weight: 400;
}

.brand.second {
  margin-left: 3px;
  color: black;
  border-radius: 4px;
  display: inline;
  padding: 1px 5px;
  /*background-color: #F89C23;*/
  background-color: #00bbff;
}

.App-intro {
  font-size: large;
}

.simple-board {
  padding: 25px;
  height: 100%;
  max-width: 1765px;
  background-color: #001e26;
}

.date,
.time {
  font: 15px helvetica, arial, sans-serif;
  width: 150px;
  display: inline-block;
  color: #bbc9cc;
}

.time {
  font-size: 20px;
}

.card {
  display: inline-block;
  color: #d6d6d6;
  background-color: #002b35;
  -webkit-box-shadow: black 0px 2px 17px 0px;
  box-shadow: black 0px 2px 17px 0px;
  width: 430px;
  min-height: 300px;
  margin: 20px 20px 0px 0px;
  position: relative;
  overflow: hidden;
  border-radius: 6px;
  vertical-align: top;
}

.card-header {
  color: #bbc9cc;
  line-height: 55px;
  height: 55px;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid #24454f;
  background-color: #013642;
}

.env-name {
  display: inline-block;
  color: #bbc9cc;
  font-size: 20px;
  font-weight: bold;
  margin-right: 20px;
}

.card-body {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 8px;
  text-align: left;
}

.card-body ul {
  padding: 0px;
  margin: 0;
  list-style: none;
  overflow: hidden;
}

.card-body li {
  margin-bottom: 10px;
  line-height: 20px;
  font-size: 14px;
  white-space: nowrap;
}

.card-body li > span {
  margin-right: 10px;
  min-width: 70px;
  display: inline-block;
  white-space: nowrap;
}

.card-body .st-triangle {
  /*border: 1px solid red;*/
  position: absolute;
  top: 140px;
  right: 60px;
  width: 80px;
  height: 60px;
}

.st-status-pass,
.st-status-fail,
.st-status-running {
  color: #289e00;
}

.st-status-fail {
  color: #f5003d;
}

.st-status-running {
  color: #00b8f5;
}

.arrow-up-pass,
.arrow-up-fail,
.arrow-up-running {
  width: 0;
  height: 0;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  border-bottom: 60px solid #289e00;
  position: relative;
}

.pass {
  color: #289e00;
  font-weight: bold;
  font-size: 18px;
}

.wait {
  color: #00b8f5;
  font-weight: bold;
  font-size: 18px;
}

.fail {
  color: #f5003d;
  font-weight: bold;
  font-size: 16px;
}
.arrow-up-fail {
  border-bottom: 60px solid #f5003d;
}

.arrow-up-running {
  border-bottom: 60px solid #00b8f5;
}

.symbol {
  width: 30px;
  height: 30px;
  position: absolute;
  color: #2e2e2e;
  font-size: 30px;
  line-height: 30px;
  right: -19px;
  top: 24px;
  font-weight: bold;
  font-family: monospace;
}

span.list-key {
  width: 70px;
  text-transform: uppercase;
  color: #547b82;
}

span.list-val {
  color: #bbc9cc;
  font-size: 16px;
}

.card-footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  font-size: 10px;
}

.image-holder,
.user-holder {
  display: inline-block;
  vertical-align: top;
}

.image-holder {
  margin-right: 10px;
}

.image-holder img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.name-holder,
.user-date {
  font-size: 16px;
  color: #bbc9cc;
}

.name-holder {
  font-weight: 600;
}

li .user-card {
  padding-bottom: 8px;
}

.spinner {
  width: 20px;
  height: 20px;
  vertical-align: bottom;
  margin-left: -7px;
}

/* status box */

div.status-box {
  border: 1px solid #212121;
  width: 150px;
  height: 170px;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  position: relative;
}

div.env-status {
  padding: 5px;
  display: inline-block;
  width: 50%;
  font-weight: bolder;
  font-size: 16px;
  margin: 10px 0px 15px;
}

div.free {
  /*background-color: #289e00;*/
  color: #289e00;
}

div.busy {
  /*background-color: #f5003d;*/
  color: #f5003d;
}

div.status-box-header {
  /*background-color: #d6d6d6;*/
  /*background-color: #2D5986;*/
  padding: 5px 0px;
  margin-bottom: 5px;
  font-size: 14px;
  border-bottom: 1px solid #262626;
  color: #999;
  text-transform: uppercase;
}
div.status-date-box {
  position: absolute;
  bottom: 10px;
  text-align: center;
  display: block;
  width: 100%;
}

div.state-date {
