}

div.header {
  white-space: nowrap;
  -webkit-box-shadow: black 0px 2px 17px 0px;
  box-shadow: black 0px 2px 17px 0px;
  border-radius: 6px;
  padding-left: 20px;
  background-color: #012b36;
}

div.header ul {
  list-style: none;
  display: block;
  text-align: left;
  margin: 0px;
  padding: 0px;
  width: 100%;
}

div.header li {
  display: inline-block;
  line-height: 55px;
  height: 53px;
  margin-right: 5%;
}

div.header .main-title {
  text-transform: uppercase;
  color: #bbc9cc;
  font-weight: bold;
  font-size: 20px;
}

div.statusBlock {
  display: inline-block;
  background-color: #01212a;
  height: 35px;
  line-height: 35px;
  width: 150px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
}

div.statusBlock > * {
  display: inline-block;
  padding: 2px 10px;
}

div.statusBlock > div.st-name {
  text-align: left;
  color: #627980;
}

div.badge {
  color: #bbc9cc;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 4px;
  display: inline;
  padding: 4px 6px;
  vertical-align: bottom;
  font-weight: 400;
}

.badge-red {
  background-color: #b60420;
}

.badge-green {
  background-color: #087103;
}

div.tests-header-container {
  position: relative;
  margin-top: 20px;
  height: 21px;
}

div.tests-header {
  color: #647b82;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  position: absolute;
}

div.tests-status {
  position: absolute;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  position: absolute;
  right: 0px;
}

div.tests-body {
  border-top: 1px solid #24454f;
}

div.tests-inner-body {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

ul.test-threads-body {
  background-color: #01212a;
  min-height: 73px;
  list-style: none;
  display: table;
  padding-left: 0px;
  width: 100%;
}

li.thread-box {
  display: table-cell;
  padding: 5px;
  width: 25px;
  line-height: 18px;
}

ul.test-thread-box {
  display: inline-block;
  padding: 7px 0px;
  min-width: 100%;
  vertical-align: middle;
}

.test-success {
  background-color: #376431;
}

.test-fail {
  background-color: #883541;
}

li.test-thread-name,
li.test-thread-value,
li.test-thread-loading {
  margin-bottom: 0px;
  text-align: center;
  display: block;
}

li.test-thread-loading {
  line-height: 50px;
}

li.test-thread-name {
  font-size: 12px;
}

li.test-thread-value {
  color: #bbc9cc;
  font-size: 20px;
  font-weight: bold;
  margin-top: 15px;
}

.bouncing_loading {
  /*display: none;*/
}

.bouncing_loading > div {
  width: 8px;
  height: 8px;
  background-color: #05aaff;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
  animation: bouncedelay 1.4s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bouncing_loading .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.bouncing_loading .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }

  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  40% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

/*************************/

div.n-msg {
  max-width: 500px;
  padding: 20px;
  position: fixed;
  display: inline-block;
  right: -550px;
  bottom: 5px;
  background-color: #002b35;
  color: #bbc9cc;
  border-radius: 6px;
  -webkit-box-shadow: black 0px 2px 17px 0px;
  box-shadow: black 0px 2px 17px 0px;
}

div.n-msg-header {
  height: 30px;
}

div.n-msg-slide {
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  right: 10px;
}

.carousel {
  margin: 0px;
  padding: 0px;
  list-style: none;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
}

.carousel li.slide {
  display: inline-block;
  vertical-align: top;
  display: inline-block;
  overflow: hidden;
  width: 100%;
  overflow: hidden;
  white-space: normal;
  text-align: center;
}

.move-bb8 {
